import Finsemble from "../../index";
import { BaseService } from "../baseService";
import RouterClient from "../../clients/routerClientInstance";
import LinkerClient from "../../clients/linkerClient";
import LauncherClient from "../../clients/launcherClient";
import Logger from "../../clients/logger";
import WindowClient from "../../clients/windowClient";
import DistributedStore from "../../clients/distributedStoreClient";

import { System } from "../../common/system";
//let util = Finsemble.Util;
//let console = new util.Console("RPC");
WindowClient.initialize();
LauncherClient.initialize();
DistributedStore.initialize();
LinkerClient.initialize();
Logger.start();

/**
 * The RPC Service makes API client calls on behalf of remote windows.
 *
 * RPC requests are sent on the FSBL.rpc channel of the inter-application bus (IAB).
 * Each request is a JSON object that contains:
 * endpoint - The API call to make. i.e. LinkerClient.publish, LauncherClient.spawn, etc
 * args - The arguments to send to the endpoint
 * callbackChannel - The IAB channel to return the response (if requested).
 *
 * If args contains ***cb*** then the arguments from the API call's callback (cb) will be returned
 * on the callbackChannel specified in the initial request with the following JSON object:
 * args - The arguments returned by the endpoint cb
 * request - The original request message (remote windows can use this for instance for matching)
 *
 * @shouldBePublished true
 * @constructor
 */
function RPCService() {
	var FSBLProxyToWindowMap = {};

	/**
	 * The RPC service keeps a local set of Client APIs called a "FSBLProxy".
	 * A FSBLProxy is created for each remote window that makes an RPC request.
	 * Basically, we're running FSBL here instead of on the client itself.
	 * This is good because some Client APIs maintain state (linker, launcher) and so they can run undisturbed, here in the service.
	 * When a command comes in, we first locate the FSBLProxy for that window.
	 * If none yet exists, then we create one, and fill it with initialized API clients.
	 *
	 * Note, all remote windows piggyback on the RouterClient for this service.
	 */
	this.getFSBLProxy = function (windowID, cb) {
		var FSBLProxy = FSBLProxyToWindowMap[windowID];
		if (FSBLProxy) {
			cb(FSBLProxy);
			return;
		}
		FSBLProxy = FSBLProxyToWindowMap[windowID] = {};
		// FSBL Clients that don't maintain state can just piggyback on the singleton clients
		// found in Finsemble.Clients
		FSBLProxy.RouterClient = RouterClient; // Piggyback
		FSBLProxy.WindowClient = WindowClient; // TODO, make one. Until we refactor WindowClient, everything will be relative to the service window - yuck
		FSBLProxy.DistributedStoreClient = DistributedStore; // Piggyback
		FSBLProxy.Logger = Logger; // Piggyback
		FSBLProxy.LauncherClient = LauncherClient.constructInstance(FSBLProxy); // Launcher needs an instance because it has state
		FSBLProxy.LinkerClient = LinkerClient.constructInstance(FSBLProxy); // Linker needs an instance because it has state
		// This is for Java (or other indirectly launched) windows to send window handles over the IAB. See the listener in FinsembleNativeService.
		FSBLProxy.Assimilation = {
			register: function (windowInformation) {
				RouterClient.transmit("FinsembleNativeService.Registration", windowInformation);
			}
		};

		// @TODO, dragAndDropClient needs an instance because it has state
		// @TODO, dialogManagerClient needs an instance because it has state

		// Handle any unspecified clients
		for (let clientName in Finsemble.Clients) {
			var client = Finsemble.Clients[clientName];
			if (!FSBLProxy[clientName]) {
				if (typeof (client.constructInstance) !== "undefined") {
					// If it can construct an instance
					FSBLProxy[clientName] = client.constructInstance(FSBLProxy);
				} else {
					// otherwise piggyback on the singleton
					FSBLProxy[clientName] = client;
				}
			}
		}

		// TODO, make this work better. We should define how baseClient.initialize() works and be able
		// to depend on it. Dependencies are tricky given how we've architected. The dependency manager
		// is built around globals, but would need to accept instances in order to work. May require some rethinking.
		FSBLProxy.LauncherClient.start(function () {
			FSBLProxy.LinkerClient.start(function () {
				cb(FSBLProxy);
			});
		});
	};
	/**
	 * Starts to listen for inbound RPC requests.
	 * @private
	 */
	this.startListening = function () {
		var self = this;
		//We listen on the FSBL.rpc channel
		fin.desktop.InterApplicationBus.subscribe("*", null, "FSBL.rpc", (message, uuid, name) => {
			// The window publishing must include an endpoint and an array of arguments, called args.
			// callbackChannel is required if ***cb*** is specified as any argument.
			if (!message) return;
			let { args, callbackChannel, endpoint } = message;
			let windowID = uuid + name;
			if (!args) {
				args = [];
			}
			Logger.system.info("RPC request", endpoint, uuid, name, message);

			// Get the FSBLProxy for the remote window
			self.getFSBLProxy(windowID, function (FSBLProxy) {
				//Since we can't execute a callback from java, c#, or whatever language, we create a new one here.
				// When the API call completes, we send the response arguments back across the IAB on the requested callbackChannel.
				let newCB = function () {
					var response = {
						args: Array.prototype.slice.call(arguments), // Convert arguments to an array
						request: message
					};
					if (callbackChannel) {
						Logger.system.info("RPC Response", uuid, name, callbackChannel, response);
						fin.desktop.InterApplicationBus.send(uuid, name, callbackChannel, response);
					} else {
						console.error("FSBL.RPC no callbackChannel specified", message);
					}
				};

				// Handle special values
				for (var i = 0; i < args.length; i++) {
					if (args[i] === "***null***") {
						args[i] = null;
					}
					if (args[i] === "***cb***") {
						args[i] = newCB;
					}
				}

				// Determine the parts of the API call. The API, and the function call. i.e. LinkerClient.publish
				let arr = endpoint.split(".");
				if (arr.length < 2) {
					console.error("FSBL.RPC bad endpoint", endpoint);
					fin.desktop.InterApplicationBus.send(uuid, name, callbackChannel, { args: { err: "Bad endpoint" }, request: message });
					return;
				}
				let api = arr[0];
				let fc = arr[1];

				if (!FSBLProxy[api]) {
					console.error("FSBL.RPC missing API", api);
					fin.desktop.InterApplicationBus.send(uuid, name, callbackChannel, { args: { err: "API not found" }, request: message });
					return;
				}
				if (!FSBLProxy[api][fc]) {
					console.error("FSBL.RPC missing function", api, fc);
					fin.desktop.InterApplicationBus.send(uuid, name, callbackChannel, { args: { err: "Function not found" }, request: message });
					return;
				}

				// Run the API call
				FSBLProxy[api][fc].apply(FSBLProxy[api], args);
			});
		});
	};

	return this;
}
RPCService.prototype = new BaseService({
	name: "RPCService",
	startup: {
		services: []
	}
});
var serviceInstance = new RPCService("RPCService");
System.ready(() => {
	//due to the way that startup works, the system may not be fully up and ready within 10 seconds. The service will error out if it isn't ready within an appropriate amount of time. No need to duplicate that here.
	//FSBL.displayStartupTimeoutError = false; <- commenting out because this causes an error.
});
serviceInstance.onBaseServiceReady(function (callback) {
	serviceInstance.startListening();
	callback();
});
serviceInstance.start();

export default serviceInstance;


